@import "../../../../../mixin.scss";
@import "../../../../../App.scss";

.customerEditSP {
  position: relative;
  .category_wrap {
    .item_wrap {
      /**
       * 
       */
      .item_body {
        /*  */
        &.item_map {
          width: 100%;
          .btn {
            display: block;
            width: 8rem;
            margin-left: auto;
          }
        }

        // 
        &.item_customer {
          @include flex_box();
          @include flex_align_start();
          > div {
            &:first-of-type {
              flex: 2;
            }
            &:not(:first-of-type) {
              margin-left: 10px;
              flex: 1;
            }
            min-width: inherit;
          }
        }

        // 
        &.item_customerRank {
          @include flex_box();
          @include flex_align_center();
          > div {
            &:first-of-type {
              // width: 160px !important;
              flex: 3;
            }
            &:not(:first-of-type) {
              margin-left: 10px;
              flex: 2;
            }
            min-width: inherit;
          }
        } //&.item_customerRank

        &.item_madori {
          > div {
            width: 160px !important;
            min-width: inherit;
          }
        }

        // 
        &.item_years {
        } //&.item_years

        // 
        &.item_remarks {
        } //&.item_remarks
      } //.item_body
      &.tags_form {
        &:not(:first-of-type) {
          margin-top: 1em;
        }
      }
    } //.item_wrap
  } //.category_wrap
} // customerEditSP
